import types from './types';

const getBlockPerHour = blockPerHour => ({
  type: types.BLOCK_CHART_HOUR,
  payload: { blockPerHour },
});
const getErroMessage = errorMessage => ({
  type: types.ERROR_MESSAGE,
  payload: errorMessage,
});

const getBlockPerMin = blockPerMin => ({
  type: types.BLOCK_CHART_MIN,
  payload: { blockPerMin },
});
const getBlockPerDay = blockPerDay => ({
  type: types.BLOCK_CHART_DAY,
  payload: { blockPerDay },
});
const getBlockAll = blockAll => ({
  type: types.BLOCK_CHART_ALL,
  payload: { blockAll },
});
const getChannel = channel => ({
  type: types.CHANNEL,
  payload: { channel },
});

const getChannelList = channelList => ({
  type: types.CHANNEL_LIST,
  payload: channelList,
});

const getDashStats = dashStats => ({
  type: types.DASHBOARD_STATS,
  payload: dashStats,
});

const getNotification = notification => ({
  type: types.NOTIFICATION_LOAD,
  payload: { notification },
});

const getPeerStatus = peerStatus => ({
  type: types.PEER_STATUS,
  payload: peerStatus,
});
const getBlockActivity = blocks => ({
  type: types.BLOCK_ACTIVITY,
  payload: blocks,
});
const getBlockNotifications = blocks => ({
  type: types.BLOCK_NOTIFICATIONS,
  payload: blocks,
});
const getTransactionByOrg = transactionByOrg => ({
  type: types.TRANSACTION_CHART_ORG,
  payload: transactionByOrg,
});

const getTransactionPerHour = transactionPerHour => ({
  type: types.TRANSACTION_CHART_HOUR,
  payload: { transactionPerHour },
});

const getTransactionPerMin = transactionPerMin => ({
  type: types.TRANSACTION_CHART_MIN,
  payload: { transactionPerMin },
});

const getTransactionPerDay = transactionPerDay => ({
  type: types.TRANSACTION_CHART_DAY,
  payload: { transactionPerDay },
});

const getTransactionAll = transactionAll => ({
  type: types.TRANSACTION_CHART_ALL,
  payload: { transactionAll },
});
const updateChannel = channel => ({
  type: types.CHANGE_CHANNEL,
  payload: { channel },
});

export default {
  getBlockPerHour,
  getBlockPerMin,
  getBlockPerDay,
  getBlockAll,
  getChannel,
  getChannelList,
  getDashStats,
  getNotification,
  getPeerStatus,
  getTransactionByOrg,
  getTransactionPerHour,
  getTransactionPerMin,
  getTransactionPerDay,
  getTransactionAll,
  updateChannel,
  getErroMessage,
  getBlockActivity,
  getBlockNotifications
};
